.footer {
    font-size: 18px;
    line-height: 1.77;
    font-weight: 500;
    z-index: 10;
    position: absolute;
    bottom: 0;
    width: 1720px;
    max-width: 100%;
    padding: 0 45px 30px;
    margin: 0 auto;
    left: 50%;
    transform: translateX(-50%);

    &__title {
        max-width: 400px;
    }

    p {
        margin-bottom: 0;
    }

    a {
        color: $accent-color;
        text-decoration: none;
    }
}
