.c-input {
    background-color: #fff;
    border: 1px solid $color-grey-300;
    border-radius: 8px;
    height: $input-height;
    font-size: 14px;
    color: #718792;
    width: 100%;
    transition: .3s ease;
    padding: 0 16px;

    &::placeholder {
        font-size: 14px;
        color: #718792;
        transition: .3s ease;
    }

    &:focus {
        border-color: $color-grey-900;

        &::placeholder {
            text-indent: 10px;
            opacity: 0;
        }
    }
}

@media (max-width: 1200px) {
    .c-input {
        font-size: 16px;
    }
}
