.c-button {
    display: inline-block;
    font-size: 14px;
    font-weight: 700;
    color: #fff;
    border: none;
    min-width: 180px;
    min-height: $input-height;
    border-radius: $input-height;
    text-align: center;
    outline: none;
    cursor: pointer;

    &--pink {
        background-color: $second-accent;

        &:active, &:hover {
            background-color: #FF186A;
        }
    }
}
