
.landing-body {
    -webkit-transition: radial-gradient 1s ease-in-out;
    -o-transition: radial-gradient 1s ease-in-out;
    transition: radial-gradient 1s ease-in-out;
}

body.hidden-content > * {
    visibility: hidden;
    opacity: 0;
}

.container {
    width: 1720px;
    max-width: 100%;
    padding: 0 45px;
    margin: 0 auto;
    height: 700px;
    min-height: 100vh;
    position: relative;
    overflow: hidden;
}

.socials-list {
    &__item {
        position: relative;
        vertical-align: middle;

        svg path {
            transition: .3s ease;
        }
    }
    &__icon:hover {
        svg path {
            fill: $accent-color;
        }
    }
    li + li {
        margin-left: 32px;
    }
}

.content {
    display: flex;
    height: 100%;

    &__section {
        position: relative;
        z-index: 2;
        display: flex;
        flex-direction: column;
        justify-content: center;
        max-width: 45%;
    }

    &__title {
        font-size: 72px;
        line-height: 1.18;
        max-width: 848px;
        margin-bottom: 32px;
        margin-top: 0;
    }

    &__subtitle {
        font-size: 18px;
        line-height: 1.77;
        max-width: 720px;
        margin-bottom: 36px;
    }

    &.has-submit {
        .content__subtitle {
            display: none;
        }

        .subscribe {
            display: none;
        }

        .success {
            display: block;
        }
    }
}

.decor {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    z-index: -1;

    img {
        max-width: none;
        position: absolute;
        top: 50%;
        right: -245px;
        transform: matrix(1, 0, 0, 1, 0, -705);
        -webkit-transform: matrix(1, 0, 0, 1, 0, -705);
        margin-top: 115px;
        max-height: 1410px;
    }
}

.letter {
    display: inline-block;
    min-width: 20px;
}

#circle {
    position: absolute;
    top: 0;
    left: 0;
    width: 800px;
    height: 800px;
    z-index: -1;
    background: radial-gradient(closest-side, rgba(59,187,233, .7), $bg-color)
}

.subscribe {

    &__form {
        display: flex;
        align-items: flex-start;

        .c-button {
            margin-left: 24px;
        }
    }

    &__inputs {
        width: 100%;
    }

}

.success {
    font-size: 32px;
    font-weight: 700;
    display: none;
    padding-bottom: 125px;

    &__title {
        color: $accent-color;
    }
}
