.header {
    padding: 48px 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    top: 0;
    left: 50%;
    width: 1720px;
    transform: translateX(-50%);
    max-width: 100%;
    z-index: 10;

    &__logo {
        display: inline-block;
        position: relative;
        max-width: 141px;

        svg {
            display: block;
            width: 100%;
        }
    }
}
