.с-input-group {
    width: 100%;

    &__message {
        display: block;
        visibility: hidden;
        opacity: 0;
        font-size: 12px;
        line-height: 1.33;
        min-height: 16px;
        margin-top: 2px;
        margin-bottom: 6px;
        transition: .3s ease;
    }

    &.has-error {

        .с-input-group__message {
            color: $color-error;
            visibility: visible;
            opacity: 1;
        }

        .c-input {
            border-color: $color-error;
            color: $color-error;
        }
    }

}
