// ------------------------------------
//   #C-CHECKBOX
// ------------------------------------

.c-checkbox {
    position: relative;
    cursor: pointer;
    vertical-align: middle;
    min-height: 16px;
    display: inline-flex;

    &__text {
        font-size: 12px;
        line-height: 1.33;
        position: relative;
        padding-top: 4px;
    }

    &__icon {
        position: relative;
        line-height: 0;
        fill: #fff;
        background-color: $color-grey-300;
        border-radius: 2px;
        flex: 0 0 20px;
        max-width: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 20px;
        width: 20px;
        margin-right: 16px;
        transition: .3s ease;

        .c-icon {
            display: block;
            opacity: 0;
            visibility: hidden;
            transform: scale(0);
            transition: .3s ease;
        }
    }

    &__input {
        display: none;

        &:checked {
            ~ .c-checkbox {
                &__icon {
                    background-color: $accent-color;
                    .c-icon {
                        visibility: visible;
                        opacity: 1;
                        transform: scale(1);
                    }
                }
            }
        }
    }
}
